import React, { useState, useEffect } from "react";
import "./support.less";
import {
  Card,
  Col,
  Input,
  Row,
  Select,
  Table,
  message,
  Upload,
  Button,
  Image,
  Popover,
  Divider,
  Tooltip,
} from "antd";
import ComPagination from "@/components/comPagination/ComPagination.jsx";
import { NavLink, useNavigate } from "react-router-dom";
import { SearchOutlined, UploadOutlined } from "@ant-design/icons";
import {
  geTickList,
  geTickType,
  getHostList,
  createOrder,
  ticketNums,
  onUrge,
  onClose,
  getTickInfo,
  replyMssage,
} from "../../api/support";
import { getUserId, upoadFile } from "../../api/common";
import moment from "moment";
import { useTranslation } from "react-i18next";
import axios from "axios";
const { Column } = Table;
const { Option } = Select;
const { TextArea } = Input;
export default function Order() {
  const { t } = useTranslation();
  const navigate = useNavigate();
  // 顶部card数据
  const [cardData, setCardData] = useState({
    pendingOrder: 0,
    processing: 0,
    pendingReply: 0,
    replied: 0,
  });
  // 表格分页数据
  const [total, setTotal] = useState(200);
  const [params, setParams] = useState({
    keywords: "",
    page: 1,
    limit: 10,
    status: "",
    // orderby: 'id',
    // sort: 'desc'
  });
  const [orderList, setorderList] = useState([]);

  const [tabHeight, settabHeight] = useState(0);
  const changePage = (page, size) => {
    const data = params;
    data.page = page;
    data.limit = size;
    setParams({ ...data });
    onGetTickList();
  };
  const onChangeOrderTable = (str) => {
    const data = params;
    data.status = str;
    data.page = 1;
    setParams({ ...data });
    onGetTickList();
  };
  useEffect(() => {
    onGetTicketNums();
    onGetTickList();
    settabHeight(updateSize());
  }, [params]);
  //获取工单
  const onGetTickList = () => {
    geTickList(params).then((res) => {
      if (res.data.status == 200) {
        const list = res.data.data.list;
        setTotal(res.data.data.count);
        setorderList([...list]);
      }
    });
  };
  const updateSize = (extraHeight, id) => {
    if (typeof extraHeight == "undefined") {
      //  默认底部分页64 + 边距10
      extraHeight = 94;
    }
    let tHeader = null;
    if (id) {
      tHeader = document.getElementById(id)
        ? document
            .getElementById(id)
            .getElementsByClassName("ant-table-thead")[0]
        : null;
    } else {
      tHeader = document.getElementsByClassName("ant-table-thead")[0];
    }
    //表格内容距离顶部的距离
    let tHeaderBottom = 0;
    if (tHeader) {
      tHeaderBottom = tHeader.getBoundingClientRect().bottom;
    }
    //窗体高度-表格内容顶部的高度-表格内容底部的高度
    // let height = document.body.clientHeight - tHeaderBottom - extraHeight
    let height = `calc(100vh - ${tHeaderBottom + extraHeight}px)`;
    return height;
  };
  //获取工单数量
  const onGetTicketNums = () => {
    ticketNums().then((res) => {
      if (res.data.status == 200) {
        const data = {
          pendingOrder: res.data.data.pending,
          processing: res.data.data.handling,
          pendingReply: res.data.data.reply,
          replied: res.data.data.replied,
        };
        setCardData(data);
      }
    });
  };
  const onSearch = (e) => {
    setParams((params) => {
      params.page = 1;
      return { ...params, keywords: e.target.value };
    });
    //setorderList([])
    //onGetTickList()
  };

  //回复
  const onReply = (item) => {
    navigate("/support/reply", { state: { id: item.id } });
  };
  const createWork = () => {
    navigate("/support/addWork");
  };

  //催单
  const reminder = (item) => {
    onUrge(item.id).then((res) => {
      console.log(res);
      if (res.data.status == 200) {
        message.success(t("tip.tip4"));
        onGetTickList();
      } else {
        message.error(res.data.data.msg);
      }
    });
  };
  //关闭工单
  const orderColose = (item) => {
    onClose({
      id: item.id,
    }).then((res) => {
      console.log(res);
      if (res.data.status == 200) {
        message.success(t("tip.tip3"));
        onGetTickList();
      } else {
        message.error(res.data.data.msg);
      }
    });
  };
  const onSearch1 = () => {
    onGetTickList();
  };

  return (
    <div className="support-content">
      <div className="top-card-wrapper">
        <Row gutter={30}>
          <Col xl={6} lg={7} md={8} sm={12} xs={24}>
            <Card bordered={false}>
              <div>
                <img src={require("@/assets/images/support/waiting.png")} />
                <div className="text">{t("support.pending")}</div>
              </div>
              <div className="count">{cardData.pendingOrder}</div>
            </Card>
          </Col>
          <Col xl={6} lg={7} md={8} sm={12} xs={24}>
            <Card bordered={false}>
              <div>
                <img src={require("@/assets/images/support/doing.png")} />
                <div className="text">{t("support.handling")}</div>
              </div>
              <div className="count">{cardData.processing}</div>
            </Card>
          </Col>
          <Col xl={6} lg={7} md={8} sm={12} xs={24}>
            <Card bordered={false}>
              <div>
                <img src={require("@/assets/images/support/replyed.png")} />
                <div className="text">{t("support.reply")}</div>
              </div>
              <div className="count">{cardData.pendingReply}</div>
            </Card>
          </Col>
          <Col xl={6} lg={7} md={8} sm={12} xs={24}>
            <Card bordered={false}>
              <div>
                <img
                  src={require("@/assets/images/support/waitingReply.png")}
                />
                <div className="text">{t("support.replied")}</div>
              </div>
              <div className="count">{cardData.replied}</div>
            </Card>
          </Col>
        </Row>
      </div>
      <div className="support-list">
        <div className="support-header">
          <div>
            <div
              className={[
                "support-state ",
                params.status == "" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("")}
            >
              {t("support.all")}
            </div>
            <div
              className={[
                "support-state ",
                params.status == "Pending" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Pending")}
            >
              <span></span>
              {t("support.pending")}
            </div>

            <div
              className={[
                "support-state ",
                params.status == "Handling" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Handling")}
            >
              <span style={{ background: "#FFA800" }}></span>
              {t("support.handling")}
            </div>

            <div
              className={[
                "support-state ",
                params.status == "Reply" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Reply")}
            >
              <span style={{ background: "#F64E60" }}></span>
              {t("support.reply")}
            </div>

            <div
              className={[
                "support-state ",
                params.status == "Replied" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Replied")}
            >
              <span style={{ background: "#4BA1FC" }}></span>
              {t("support.replied")}
            </div>

            <div
              className={[
                "support-state ",
                params.status == "Resolved" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Resolved")}
            >
              {t("support.resolved")}
            </div>
            <div
              className={[
                "support-state ",
                params.status == "Closed" ? "support-state-bold" : "",
              ].join("")}
              onClick={() => onChangeOrderTable("Closed")}
            >
              {t("support.closed")}
            </div>
          </div>

          <div>
            <div>
              <Input
                size="large"
                placeholder={t("input")}
                prefix={<SearchOutlined onClick={() => onSearch1()} />}
                onPressEnter={(e) => onSearch(e)}
                className="order-search-input"
                onChange={(e) => (params.keywords = e.target.value)}
              />
            </div>

            <div className="support-add" onClick={() => createWork()}>
              <div className="icons">
                <div className="circle-hui"></div>
                <div className="circle-white"></div>
              </div>
              {t("support.addWork")}
            </div>
          </div>
        </div>
        <Table
          dataSource={orderList}
          rowKey={(record) => record.id}
          pagination={false}
          // scroll={{ x: "max-content", y: tabHeight }}
        >
          <Column
            title={t("support.workType")}
            dataIndex="name"
            key="name"
            width={100}
            align={"center"}
          />
          <Column
            title={t("support.workTitle")}
            dataIndex="title"
            key="title"
            width={500}
            render={(tags, data) => (
              <>
                <Tooltip
                  placement="top"
                  title={"#" + data.ticket_num + "-" + data.title}
                >
                  <div
                    style={{
                      width: 600,
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                    }}
                  >
                    {"#" + data.ticket_num + "-" + data.title}
                  </div>
                </Tooltip>
              </>
            )}
          ></Column>
          <Column
            title={t("support.workTime")}
            dataIndex="post_time"
            key="post_time"
            width={150}
            render={(tags) => (
              <>{moment(tags * 1000).format("YYYY-MM-DD HH:mm")}</>
            )}
          ></Column>

          <Column
            title={t("support.status")}
            dataIndex="status"
            key="status"
            width={80}
            render={(tags) => (
              <>
                <div
                  className="status-tag"
                  style={{
                    background:
                      tags == "Pending"
                        ? "#C9F7F5"
                        : tags == "Handling"
                        ? "#FFF4DE"
                        : tags == "Reply"
                        ? "#FFE2E5"
                        : "#F2F2F7",
                    color:
                      tags == "Pending"
                        ? "#1BC5BD"
                        : tags == "Handling"
                        ? "#FFA800"
                        : tags == "Reply"
                        ? "#F64E60"
                        : "#9696A3",
                  }}
                >
                  {tags == "Pending"
                    ? t("support.pending")
                    : tags == "Handling"
                    ? t("support.handling")
                    : tags == "Reply"
                    ? t("support.reply")
                    : tags == "Replied"
                    ? t("support.replied")
                    : tags == "Resolved"
                    ? t("support.resolved")
                    : t("support.closed")}
                </div>
              </>
            )}
          ></Column>
          <Column
            title={t("support.action")}
            key="action"
            width={80}
            render={(tags, index) => (
              <>
                <div className="actions">
                  <div className="action-model">
                    <div onClick={() => onReply(index)}>
                      {t("support.replyBtn")}
                    </div>
                    <div onClick={() => reminder(index)}>
                      {t("support.reminderBtn")}
                    </div>
                    <div onClick={() => orderColose(index)}>
                      {t("support.closeBtn")}
                    </div>
                  </div>
                  <div className="text" style={{ color: "#5C5EE4" }}>
                    ...
                  </div>
                </div>
              </>
            )}
          ></Column>
        </Table>
      </div>
      <div className="bottom-table-wrapper">
        <ComPagination
          total={total}
          page={params}
          changePage={changePage}
        ></ComPagination>
      </div>
    </div>
  );
}
